<template>
  <div class="position">
      <div class="title">Начальная позиция</div>
      <div class="radio-toolbar">
        <input type="radio" id="radioStart" name="radioPosition" value="start" :checked="isSelected('start')">
        <label for="radioStart" @click="onSelect('start')">Стартовая</label>

        <input type="radio" id="radioFen" name="radioPosition" value="fen" :checked="isSelected('fen')">
        <label for="radioFen" @click="onSelect('fen')">Заданная</label>

        <input type="radio" id="radioArbitrary" name="radioPosition" value="orange" :checked="isSelected('arbitrary')">
        <label for="radioArbitrary" @click="onSelect('arbitrary')">Произвольная</label> 
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Types from '../store/modules/chessboard/types'

export default {
  name: 'PositionSelect',
  computed: {
    ...mapGetters({
      positionType: Types.getters.GET_POSITION_TYPE
    })
  },
  methods: {
    isSelected(type) {
      return this.positionType === type;
    },
    onSelect(type) {
      this.$store.dispatch(Types.actions.SET_POSITION_TYPE, type);
    }
  }
}
</script>

